import React, { useRef } from 'react';
import emailjs from '@emailjs/browser';
// emailjs est utilis pour envoyer le formulaire de contact par e-mail. La bibliothque simplifie le processus d'envoi de courriels en utilisant des modles prdfinis ( template_5tmnj4e) et un service configur ( service_esiy8ak). La fonction sendEmailest dclenche lors de la soumission du formulaire, et elle utilise la rfrence du formulaire ( form.current) pour collecter les donnes du formulaire et les envoyer via emailjs.

const Contact = ({ resumeBasicInfo, resumeContact }) => {
  let sectionName = '';
  let contactName = '';
  let contactNamePlaceholder = '';
  let contactEmail = '';
  let contactEmailPlaceholder = '';
  let contactMessage = '';
  let contactMessagePlaceholder = '';
  let contactSubmit = '';

  if (resumeBasicInfo && resumeContact) {
    sectionName = resumeBasicInfo.section_name.contact;
    contactName = resumeContact.name;
    contactNamePlaceholder = resumeContact.name_placeholder;
    contactEmail = resumeContact.email;
    contactEmailPlaceholder = resumeContact.email_placeholder;
    contactMessage = resumeContact.message;
    contactMessagePlaceholder = resumeContact.message_placeholder;
    contactSubmit = resumeContact.submit;
  }

  const form = useRef();
  // permet de crer une rfrence qui peut tre utilise pour accder  des lments du DOM ou  d'autres valeurs dans le composant, et elle persiste entre les rendus du composant sans dclencher un rendu supplmentaire.
  const sendEmail = (e) => {
    e.preventDefault();

    const isConfirmed = window.confirm(
      'Voulez-vous vraiment envoyer ce message ?'
    );

    if (isConfirmed) {
      emailjs
        .sendForm(
          'service_esiy8ak',
          'template_5tmnj4e',
          form.current,
          'Ob93Uttl879enZhge'
        )
        .then(
          (result) => {
            console.log(result.text);
            alert('Message envoy avec succs !');
          },
          (error) => {
            console.log(error.text);
            alert("Une erreur s'est produite lors de l'envoi du message.");
          }
        );
    }
  };

  return (
    <section
      id='contact'
      className='pb-5'
    >
      <div className='col-md-12 mx-auto'>
        <div className='col-md-12'>
          <h1 className='section-title'>
            <span>{sectionName}</span>
          </h1>
        </div>
      </div>
      <div className='col-md-8 mx-auto'>
        <form
          ref={form}
          onSubmit={sendEmail}
        >
          <div className='form-group'>
            <label htmlFor='name'>{contactName}</label>
            <input
              type='text'
              className='form-control'
              id='name'
              name='name'
              placeholder={contactNamePlaceholder}
              autoComplete='name'
            />
          </div>
          <div className='form-group'>
            <label htmlFor='email'>{contactEmail}</label>
            <input
              type='email'
              className='form-control'
              id='email'
              name='email'
              placeholder={contactEmailPlaceholder}
              autoComplete='email'
            />
          </div>
          <div className='form-group'>
            <label htmlFor='message'>{contactMessage}</label>
            <textarea
              className='form-control'
              id='message'
              rows='5'
              name='message'
              placeholder={contactMessagePlaceholder}
              autoComplete='message'
            ></textarea>
          </div>
          <button
            type='submit'
            className='btn'
          >
            {contactSubmit}
          </button>
        </form>
      </div>
    </section>
  );
};
export default Contact;
